@import url(~basscss/css/basscss.min.css);
* {
  box-sizing: border-box; }

:root {
  --color-brand: #e91e63;
  --color-link: #1971c2;
  --color-brand-rgb: 233, 30, 99;
  --color-black: #172b4d;
  --color-white: #ffffff;
  --color-gray-0: #fbfbfb;
  --color-gray-1: #f4f5f7;
  --color-gray-2: #eaecef;
  --color-gray-3: #cbcdd4;
  --sidebar-width: 18rem;
  --border-radius-md: 6px; }

html,
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  color: var(--color-black);
  font-size: 16px; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
pre.code {
  font-weight: normal;
  margin-top: 0;
  margin-bottom: var(--space-2);
  padding-top: var(--space-1); }

.menu {
  height: 28px;
  width: 60px;
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
  background: none;
  border: 0;
  text-align: center;
  outline: none;
  color: transparent; }
  .menu:before, .menu:after,
  .menu div {
    content: "";
    position: absolute;
    width: 50px;
    height: 4px;
    background-color: var(--color-black);
    transition: all ease 0.3s;
    border-radius: 4px; }
  .menu:before {
    top: 0; }
  .menu:after {
    bottom: 0; }
  .menu.active div {
    opacity: 0; }
  .menu.active:before {
    transform: rotate(-45deg) translate3d(0, -50%, 0);
    top: 50%; }
  .menu.active:after {
    transform: rotate(45deg) translate3d(0, 50%, 0);
    bottom: 50%; }

h1.main {
  font-size: 2.2rem;
  padding: 0; }

p,
pre.code {
  margin-top: 0;
  padding-top: 0; }

pre.code {
  border-radius: var(--border-radius-md);
  padding: var(--space-2);
  position: relative;
  overflow: auto; }
  pre.code .prism-code--copy {
    position: absolute;
    top: 0;
    right: 0;
    padding: var(--space-1);
    color: var(--color-white);
    cursor: pointer; }

a {
  color: var(--color-link);
  text-decoration: none; }
  a:focus, a:active {
    text-decoration: dashed; }

.rounded {
  border-radius: var(--border-radius-md); }

.sidebar,
.container {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  overflow-y: auto; }

.sidebar {
  max-width: var(--sidebar-width);
  position: fixed;
  background: var(--color-gray-1); }

.search input {
  width: 100%;
  line-height: 1.8rem;
  font-size: 1.2rem;
  color: var(--color-black);
  border-radius: var(--border-radius-md);
  border: 2px solid var(--color-gray-3);
  background: var(--color-gray-2);
  outline: none; }
  .search input::placeholder {
    opacity: 0.7; }
  .search input:focus {
    border-color: rgba(var(--color-brand-rgb), 0.5);
    background: var(--color-gray-0); }
    .search input:focus::placeholder {
      opacity: 0; }

.sidebar--links {
  margin: 0;
  padding: var(--space-2);
  padding-top: 0;
  list-style-type: none;
  width: 100%; }
  .sidebar--links li,
  .sidebar--links a {
    display: block; }
  .sidebar--links a {
    line-height: 1.7rem;
    color: var(--color-black);
    border: 2px solid transparent;
    outline: none;
    transition: all 0.1s ease; }
    .sidebar--links a:focus {
      border-color: rgba(var(--color-brand-rgb), 0.5);
      background: rgba(var(--color-brand-rgb), 0.05); }
    .sidebar--links a.active {
      border-color: transparent;
      background: var(--color-gray-2);
      font-weight: 600; }

.container {
  padding-left: var(--sidebar-width);
  background: var(--color-gray-0); }

.brand a {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.7rem;
  color: var(--color-brand); }
  .brand a svg {
    width: 4rem; }
  .brand a span {
    margin-left: var(--space-2); }

.brand .brand--navmenu {
  display: none; }

.iconset--info {
  margin-bottom: var(--space-2); }
  .iconset--info td,
  .iconset--info th {
    padding: var(--space-1) 0;
    min-width: 85px;
    text-align: left; }

.icons {
  display: grid;
  grid-auto-rows: auto;
  grid-auto-columns: max-content;
  grid-template-columns: repeat(auto-fill, minmax(90px, 1fr));
  grid-column-gap: var(--space-2);
  grid-row-gap: var(--space-2);
  text-align: center; }
  .icons .item {
    outline: none; }
    .icons .item .icon {
      min-height: 64px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--color-white);
      border-radius: var(--border-radius-md);
      box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
      border: 2px solid transparent;
      font-size: 1.6em; }
    .icons .item:hover .icon {
      background: var(--color-white); }
    .icons .item:focus .icon {
      border-color: rgba(var(--color-brand-rgb), 0.5);
      background: rgba(var(--color-brand-rgb), 0.05); }
    .icons .item:focus .name {
      color: var(--color-brand); }
  .icons .name {
    margin-top: 0.5rem;
    font-size: 0.7em;
    overflow: hidden;
    user-select: all; }

@media (max-width: 719px) {
  .container,
  .sidebar {
    min-height: auto;
    width: 100%;
    max-width: 100%;
    position: relative;
    padding: 0; }
  .sidebar {
    padding-top: var(--space-2); }
  .container > .p3 {
    padding: var(--space-2); }
  .brand {
    padding: 0 var(--space-2);
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .brand .brand--navmenu {
      display: block; }
  .sidebar--links {
    display: none; }
    .sidebar--links.active {
      display: block; }
  .prism-code--copy {
    display: none; } }

